import React, { useEffect, useState } from 'react'
import './changeLanguageButton.css'
import Switch from '@material-ui/core/Switch'
import { withNamespaces } from 'react-i18next'
import i18n from '../../i18n'

function ChangeLanguage(props) {
  const [isPortuguese, setIsPortuguese] = useState(true)

  useEffect(() => {
    if(isPortuguese){
      i18n.changeLanguage('pt')
    }
    else{
      i18n.changeLanguage('en')
    }
  }, [isPortuguese])

  return (
    <div id='change-language-button'>
      <img src='./images/brazil-flag.png' className='flag'/>
 
      <Switch
        checked={!isPortuguese}
        onChange={() => {setIsPortuguese(!isPortuguese)}}
        value="pt_BR"
        color='default'
      />

      <img src='./images/united-states-flag.png' className='flag'/>
    </div>
  )
}

export default withNamespaces()(ChangeLanguage)