import React from 'react';
import Panel from "../../Components/Panel";
import FadeInAnimation from '../../Components/FadeInAnimation';
import NextSectionArrow from '../../Components/NextSectionArrow'
import './countdownGifGenerator.css'
import { withNamespaces } from 'react-i18next'

function CountdownGifGenerator(props){
  return(
    <Panel id={props.id} backgroundColor='#9D91FF'>
      <>
        <FadeInAnimation
          desktopAnimation={'FadeInLeftToRight'}
          mobileAnimation={'FadeInBottomUp'}
          wrapperClasses={`col-9 no-gutters d-flex flex-column-reverse flex-sm-row
            justify-content-between align-items-center`}
          id={'magento-2-samples'}
        >
          <div>
            <h2>{props.t("Gerador de GIF's de contagem regressiva")}</h2>
            <br/>
            <p>
              {props.t("Descrição Gerador de GIF's de contagem regressiva")}
              <br/>
              <br/>
              {props.t('Feito em NodeJS')}
            </p>
            <br/>
            <a href='https://github.com/CaioAFA/node-countdown-gif-generator' rel="noreferrer" target='_blank'>
              <b>{props.t('Acessar Repositório')}</b>
            </a>
          </div>

          <img id='countdown-img' alt='countdown-img' src='./images/countdown.jpg' />
        </FadeInAnimation>

        <NextSectionArrow goTo={props.nextSectionId} />
      </>
    </Panel>
  )
}

export default withNamespaces()(CountdownGifGenerator)